    <p>
      基础知识就是3d 需要场景 + 镜头
    </p>
    <p>
      场景 需要 网格对象 mesh + 灯光
    </p>
    <p>
      网格对象 由 几何体geometry + 纹理材质material 组成
      <a href="https://threejs.org/manual/#zh/fundamentals">https://threejs.org/manual/#zh/fundamentals</a>
    </p>

    <highlightjs
